<template>
  <div>
    <BackToHomeButton msg="You are filtering bookmarks. "/>

    <v-container class="mx-auto">

      <!--  -->
      <FilterWebsiteData
          :user-to-select="userToSelect"
          :count="count"
          @filterNewRequestSend="filterNewRequestSend"
      ></FilterWebsiteData>

      <v-col
          v-for="(item, i) in items"
          :key="i"
          cols="12"
      >
        <!--  -->
        <WebsiteSearchResults :item="item"/>
      </v-col>

      <!--  -->
      <v-btn
          block
          color="yellow"
          v-show="showMoreBtn"
          @click="filterSendRequest"
      >
        <v-icon left>
          mdi-refresh
        </v-icon>
        More
      </v-btn>
    </v-container>

    <ToTopButton/>
  </div>
</template>

<script>

import FilterWebsiteData from "@/component/FilterWebsiteData";
import WebsiteSearchResults from "@/component/WebsiteSearchResults";
import BackToHomeButton from "@/component/BackToHomeButton";
import ToTopButton from "@/component/ToTopButton";
import moment from "moment";

export default {
  name: "FilterPage",
  components: {ToTopButton, BackToHomeButton, WebsiteSearchResults, FilterWebsiteData},
  data: () => ({
    items: [],
    //  userName  bookmarkNumber 
    userToSelect: [],
    //  10  10
    filterLoad: 10,
    // 
    count: 0,
    // usernames, dates, ifOrderByTime, ifDesc
    usernames: [],
    dates: [],
    ifOrderByTime: false,
    ifDesc: false,
  }),
  methods: {
    // 
    loadAllUser() {
      this.axios.post("/user/usernames-and-bookmarks").then(res => {
        this.userToSelect = res.data;
      });
    },

    // 
    filterNewRequestSend(usernames, dates, ifOrderByTime, ifDesc) {
      // 
      this.filterLoad = 10;
      this.count = 0;
      // 
      this.usernames = usernames;
      this.dates = dates;
      this.ifOrderByTime = ifOrderByTime;
      this.ifDesc = ifDesc;
      this.filterSendRequest();
    },
    // 
    filterSendRequest() {
      let data = {
        usernames: this.usernames,
      };
      let fromTime = null;
      let toTime = null;

      let firstTime = this.dates[0];
      if (firstTime !== undefined) {
        fromTime = moment(firstTime).valueOf();
      }

      let secondTime = this.dates[1];
      if (secondTime !== undefined) {
        toTime = moment(secondTime).valueOf();
      }

      this.axios.post("/home/filter", data, {
        params: {
          fromTimestamp: fromTime,
          toTimestamp: toTime,
          load: this.filterLoad,
          orderField: this.ifOrderByTime ? "creation_time" : "user_name",
          order: this.ifDesc ? "desc" : "asc"
        }
      }).then(res => {
        // 
        let webs = res.data;
        this.items = webs;
        // 
        let count = webs.length;
        if (count === 0) {
          alert("No Result Found...");
          // 
          this.filterLoad = 10;
          this.count = 0;
        } else if (count === this.count) {
          alert("No More Results");
        } else {
          // 
          this.count = count;
          //  10
          this.filterLoad += 10;
        }
      });
    },
  },

  computed: {
    showMoreBtn() {
      return this.count !== 0
          && this.count % 10 === 0
      //  filterLoad  10
      //  10 
    }
  },

  created() {
    this.loadAllUser();
  }
}
</script>