          color="black"
          dark
          @click="goToPopularPage"
      >
        <v-icon left>
          mdi-fire
        </v-icon>
        Popular
      </v-btn>
      <v-divider vertical style="margin-left: 1%;margin-right: 1%"></v-divider>
      <v-btn
          rounded
          outlined
