<script>
export default {
  name: 'HomePageButtons',

  methods:{
    // 
    exportHtmlFile() {
      //  3000  /file  3000 
      window.open("/file?username=" + this.toUserName, "_blank");
    },
    // 
    openAllTagsPage() {
      this.$router.push("/all-tags");
    },
    //  filter 
    goToFilter() {
      this.$router.push("/filter");
    },
    // 
    goToPopularPage() {
      this.$router.push("/popular");
    }
  },

  props: {
    currentUser: {},
    findAll: {},
    findMine: {},
    findOthers: {},
    isOut: {},
    recent: {},
    toUserName: {}
  }
}
</script>